<template>
  <v-switch
    v-if="showToggle"
    color="blue darken-2"
    inset
    v-model="$vuetify.theme.dark"
    @change="setDark()"
    :label="`Dark Theme (Experimental)`"
  ></v-switch>
</template>

<script>
import { mapActions } from "vuex";

export default {
  name: "ThemeToggle",
  data: () => ({
    showToggle: false
  }),
  methods: {
    ...mapActions(["setDarkTheme"]),

    async setDark() {
      console.log(" -- Setting Dark Theme");
      console.log(this.$vuetify.theme.dark);
      await this.setDarkTheme(this.$vuetify.theme.dark);
    }
  }
};
</script>

