<template>
  <div>
    <v-row class="pa-0" justify="center" align="start">
      <v-col cols="12" md="5" sm="12">
        <v-card>
          <v-card-title class="justify-center">User Information</v-card-title>
          <CustomerAddress />
          <v-card-actions>
            <v-btn depressed dark color="blue darken-2" :to="{name:'ChangeUserDetails'}">
              <v-icon class="mr-2">edit</v-icon>Edit Details
            </v-btn>
            <v-spacer></v-spacer>
            <v-btn depressed dark color="blue darken-2" :to="{name:'ChangePassword'}">
              <v-icon class="mr-2">edit</v-icon>Password
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
      <v-col cols="12" md="7" sm="12">
        <v-card>
          <v-card-title class="justify-center">Account Management</v-card-title>
          <v-card-text></v-card-text>
          <v-card-actions>
            <v-btn block dark color="blue darken-2" :loading="loadingSurvey" @click="getSurvey()">
              <v-icon class="mr-2">edit</v-icon>Marketing Preferences
            </v-btn>
          </v-card-actions>
          <v-card-actions>
            <v-dialog v-model="confirmDeleteDialog" max-width="500">
              <template v-slot:activator="{ on }">
                <v-btn block color="error" @click.stop="confirmDeleteDialog=true">
                  <v-icon class="mr-2">delete_forever</v-icon>Delete User Account
                </v-btn>
              </template>
              <v-card>
                <v-card-title class="justify-center">
                  <span class="mt-4 title">
                    This Action cannot be undone!
                    <br />
                  </span>
                  <span class="body-2 mt-3">Are you sure to Delete your account?</span>
                </v-card-title>
                <v-divider class="mt-3"></v-divider>
                <v-card-actions>
                  <v-btn color="info" @click.stop="confirmDeleteDialog=false">Close</v-btn>
                  <v-spacer></v-spacer>
                  <v-btn color="error" @click.stop="deleteAccount">Delete</v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-card-actions>
        </v-card>
        <CustomerDeliveryDays class="mt-2" />
      </v-col>
    </v-row>
    <GDPR :getSurveyData="getSurveyData" :forceShow="true"></GDPR>
  </div>
</template>

<script>
import axios from "axios";
import GDPR from "@/components/account/GDPR.vue";
import CustomerDeliveryDays from "@/components/account/CustomerDeliveryDays.vue";
import CustomerAddress from "@/components/customer/CustomerAddress.vue";
import { mapGetters, mapActions } from "vuex";
export default {
  name: "Account",
  components: {
    GDPR,
    CustomerAddress,
    CustomerDeliveryDays
  },
  data: () => ({
    loadingSurvey: false,
    showSurvey: false,
    getSurveyData: {},
    confirmDeleteDialog: false
  }),
  computed: {
    ...mapGetters(["retrieveUser"])
  },
  methods: {
    ...mapActions(["deleteUserAccount", "setSnackbar"]),
    async getSurvey() {
      this.showSurvey = true;
      this.loadingSurvey = true;

      console.log(" -- Get Survey");
      let stringPayload = JSON.stringify({
        userId: this.retrieveUser.userNum,
        sessionKey: this.retrieveUser.sessionKey
      });
      const params = new URLSearchParams();
      params.append("params", stringPayload);

      await axios
        .post("get-survey", params)
        .then(response => {
          if (response.data.success == true) {
            this.getSurveyData = response.data;
            return response.data.success;
          } else {
            return response.data.success;
          }
        })
        .catch(e => {
          console.log(e);
          return false;
        });

      this.loadingSurvey = false;
      this.showSurvey = true;
    },
    deleteAccount() {
      this.confirmDeleteDialog = false;
      const rec = Object.assign(
        {},
        { userId: this.retrieveUser.userNum },
        { sessionKey: this.retrieveUser.sessionKey }
      );
      this.deleteUserAccount(rec)
        .then(response => {
          if (response.success == true) {
            //clear all user data in store
            this.setSnackbar({
              text: "Your account has been deleted successfully"
            });
            this.$router.push({ name: "SignIn" });
          } else {
            this.setSnackbar({ color: "error", text: response.message });
          }
        })
        .catch(er => {
          console.log(er);
        });
    }
  }
};
</script>

