<template>
  <v-switch
    color="blue darken-2"
    inset
    v-model="productCode"
    @change="setCode()"
    :label="`Show Product Code`"
  ></v-switch>
</template>

<script>
import { mapActions } from "vuex";

export default {
  name: "ProductCodeToggle",
  data() {
    return {
      productCode: this.$store.getters.retrieveProductCode
    };
  },
  methods: {
    ...mapActions(["setProductCode"]),

    async setCode() {
      await this.setProductCode(this.productCode);
    }
  }
};
</script>

