<template>
  <div>
    <v-row justify="center" align="center">
      <v-col lg="10" md="12" sm="12">
        <v-card elevation="10" shaped>
          <v-card-title>
            <v-icon class="mr-2">info</v-icon>Important Information
          </v-card-title>
          <v-card-text>The weight of some goods may vary marginally from that ordered and the price may change to reflect actual weight - if it weighs a little less you pay a little less and if it weighs a little more you pay a little more. Prices in ‘My basket’ are estimates only, however price per kilo are displayed.</v-card-text>
        </v-card>
      </v-col>
    </v-row>

    <v-row v-for="(faqType,i) in faqsType" :key="i" justify="center" align="center">
      <v-col cols="12" lg="10" md="12" sm="12">
        <span class="title font-weight-bold">
          <v-icon class="mr-2">help_outline</v-icon>
          {{faqType == "NA"?'General Questions' :faqType}}
        </span>
        <v-expansion-panels accordion class="my-2">
          <v-expansion-panel v-for="item in faqs[faqType]" :key="item.id">
            <v-expansion-panel-header class="subtitle-2">
              {{item.question}}
              <template v-slot:actions>
                <v-icon color="info">add</v-icon>
              </template>
            </v-expansion-panel-header>
            <v-expansion-panel-content class="body-2 mt-4">
              <span v-html="item.answer"></span>
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-expansion-panels>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import axios from "axios";

export default {
  name: "FAQ",

  computed: {
    faqs() {
      if (this.$store.getters.retrieveFAQ) {
        return this.$store.getters.retrieveFAQ.items;
      } else {
        return [];
      }
    },
    faqsType() {
      return Object.keys(this.faqs);
    }
  },

  activated() {
    this.getFAQ().then(() => {
      console.log(" -- Get Survey");
      let stringPayload = JSON.stringify({
        userId: this.$store.getters.retrieveUser.userNum,
        sessionKey: this.$store.getters.retrieveUser.sessionKey
      });
      const params = new URLSearchParams();
      params.append("params", stringPayload);

      axios
        .post("get-survey", params)
        .then(response => {
          if (response.data.success == true) {
            this.getSurveyData = response.data;
            return response.data.success;
          } else {
            return response.data.success;
          }
        })
        .catch(e => {
          console.log(e);
          return false;
        });
    });
  },

  methods: {
    ...mapActions(["getFAQ"])
  }
};
</script>

